import './App.css';
import 'bootstrap/dist/css/bootstrap.css';
import React, { useState } from 'react';
import './css/app_style.min.css'


function App() {
  const [append, setAppend] = useState('0');
  const [formula, setFormula] = useState('');
  const [operator, setOperator] = useState(false);
  const [choiceOperator, setChoiceOperator] = useState('') 
  const [decimal, setDecimal]=useState(false)
  const [answerFind, setAnswerFind] = useState(false) 

  function clear(){
    setAppend('0')
    setFormula('')
    setOperator(false)
    setChoiceOperator('')
    setDecimal(false)
    setAnswerFind(false)
  }

  function appendNumber(e){    
    const value=e.target.textContent.trim();
    setOperator(false)
    setChoiceOperator('')    
    if(append==='0'){
      setAppend(`${value}`)      
      setFormula(`${formula+value}`)
      setAnswerFind(false)
    }else if (append==='0.'){
      setAppend(`${(append+value)}`)
      setFormula(`${(formula+value)}`)
    }else if (answerFind){
      setAppend(`${value}`)      
      setFormula(`${value}`)
      setAnswerFind(false)
    }else if (operator){
      if(choiceOperator.length===2){
        setAppend(`${append+value}`)  
      }else{
        setAppend(`${value}`)        
      }     
      setFormula(`${(formula+value)}`) 
    }
    else{   
    setAppend(`${(append+value).replace(/^0+/,'')}`)
    setFormula(`${(formula+value).replace(/^0+/,'')}`)
    }      
  }

    function operatorChoice(e){           
      const value=e.target.textContent.trim();
      setChoiceOperator(`${value}`)  
      setAppend(`${value}`)
      if(!operator){ 
        if(choiceOperator!==`${value}`){        
          if (answerFind){          
            const positionEqual=formula.indexOf('=') 
            setFormula(`${formula.substr(positionEqual+1,formula.length)+value}`)
            setAnswerFind(false)
          }else{
           setFormula(`${(formula+value)}`)  
          }  
        }
      }else{
        if((value==='-')&(choiceOperator.length<2)){
          console.log("Choice operator : "+choiceOperator.length)

          setAppend(value)
          setChoiceOperator(`${choiceOperator+value}`)
          setFormula(`${formula+value}`)
        }
        else{
          console.log("else")
          setChoiceOperator(`${value}`)
          setFormula(`${formula.substr(0,formula.length-choiceOperator.length)+value}`)
        }
      }
      setOperator(true)
      setDecimal(false)          
    }

    function answer(e){
      const lastElementInFormula=parseFloat(formula.charAt(formula.length-1))    
      const value=e.target.textContent.trim();
       
      if ((!answerFind) & (!(isNaN(lastElementInFormula)))){             
        const regex1 = /x/gm
        const subst1='*'   
        let formulaModify=formula.replace(regex1,subst1)      //gestion du signe x
        // eslint-disable-next-line no-eval
        let result=(Math.round(100000000*eval(formulaModify))/100000000).toString()         
        setAppend(result)
        setFormula(`${(formula+value+result)}`)
        setAnswerFind(true)
        setDecimal(false)
        setChoiceOperator('')
      }      
    }

    function decimalButton(e){
      const value=e.target.textContent.trim();      
      if (!decimal){
        setAppend(`${(append+value)}`)
        if (append==='0'){                 
          setFormula(`${('0'+value)}`)
        }
        else if (operator){         
          setFormula(`${(formula+'0.')}`)
          setAppend(`${'0.'}`)
        }
        else{
          setFormula(`${(formula+value)}`)}        
          setDecimal(true)
      }     
    }

  return (   
    <div className="App container" id="calculatrice">
            
        <div className="formulaScreen text-right">
              {formula}
        </div>
        <div className="outputScreen text-right" id="display">
              {append}
        </div>

        <div className="buttonArray">
     
          <div className="row">
            <div className="col-6 btn" id="clear" onClick={clear}>C</div>
            <div className="col btn" id="decimal" onClick={decimalButton}>.</div>
            <div className="col btn operator" id="divide" onClick={operatorChoice}>/</div>
          </div>
          <div className="row">
            <div className="col btn" id="seven" onClick={appendNumber}>7</div>
            <div className="col btn" id="eight" onClick={appendNumber}>8</div>
            <div className="col btn" id="nine" onClick={appendNumber}>9 </div>
            <div className="col btn operator" id="multiply" onClick={operatorChoice}>x</div>
          </div>
          <div className="row">
            <div className="col btn" id="four" onClick={appendNumber}>4</div>
            <div className="col btn" id="five" onClick={appendNumber}>5</div>
            <div className="col btn" id="six" onClick={appendNumber}>6</div>
            <div className="col btn operator" id="subtract" onClick={operatorChoice}>-</div>
          </div>
          <div className="row">
            <div className="col btn" id="one" onClick={appendNumber}>1</div>
            <div className="col btn" id="two" onClick={appendNumber}>2</div>
            <div className="col btn" id="three" onClick={appendNumber}>3</div>
            <div className="col btn operator" id="add" onClick={operatorChoice}>+</div>
          </div>
          <div className="row">
            <div className="col-6 btn" id="zero" onClick={appendNumber}>0</div>
            <div className="col-6 btn" id="equals" onClick={answer}>=</div>          
          </div>
          </div>
      
    </div>
  );
}

export default App;